:root {
  --yellow: #fed700;
  --black: #000;
  --black--primary: #333e48;
  --black--secondary: #111827;
  --black--third: #323232;
  --white: #fff;
  --gray: #7d8184;
  --gray--light: #ddd;
  --gray--light1: #f5f5f5;
  --gray--light2: #eaeaea;
  --gray--darker: #768b9e;
  --gray--secondary: #777;
  --blue: #0062bd; 
  --red: #dc3545;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

html {
  font-size: 10px;
}

a {
  text-decoration: none;
}

header {
  border-bottom: 1px solid var(--gray--light2);
}

.header_container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_logo {
  width: 239px;
  height: 20px;
}

.header_logo img {
  width: 100%;
}

.phone_number {
  display: flex;
  align-items: center;
  justify-content: center;
}
.phone_number button {
  font-weight: 600;
  color: var(--gray--secondary);
  border: none;
  background: none;
  margin-left: 5px;
  margin-right: 5px;
  cursor: pointer;
  transition: 0.3s;
}
.phone_number button:hover {
  color: var(--yellow);
}

.headset {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.number {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 700;
  margin-left: 17px;
  margin-right: 27px;
}

 .number span {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 400;
}

.phone_number .num {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 400;
}
.phone_number .go_to_contact {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 600;
  text-decoration: none;
  position: relative;
}
.phone_number .go_to_contact::before {
  position: absolute;
  content: " ";
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: var(--black--primary);
}

.lang_menu {
  padding-left: 16px;
}

/* burger-menu */
.burger_menu {
  display: none;
  position: relative;
  font-size: 1.2rem;
  cursor: pointer;
}
.burger_class {
  display: block;
  width: 30px;
  height: 2px;
  border-radius: 8px;
  background: #fed700;
}
.burger_class:nth-child(2) {
  margin: 5px 0;
}


/* Hero */

.menu_container {
  width: 100%;
  background-color: var(--yellow);
  padding: 4px 0;
}

.menu_wrapper {
  max-width: 1180px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 25px;
}

.head_of_menu {
  padding: 0 10px;
  background-color: var(--white);
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 600;
  border-radius: 4px 4px 0 0;
  cursor: pointer;
  /* user-select: none; */
}

.head_of_menu_black,
.resp_catalog_style {
  padding: 0 10px;
  background-color: var(--black--primary);
  color: var(--white);
  font-size: 1.6rem;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  /* user-select: none; */
}

.head_of_menu div,
.head_of_menu_black div,
.resp_catalog_style div {
  height: 40px;
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 0 4px;
}

.resp_catalog {
  display: none;
}

.catg_menu,
.resp_catalog {
  position: relative;
  min-width: 312px;
}

.item_link {
  width: 292px;
  height: 44.2px;
  color: var(--gray);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid var(--gray--light);
  padding: 0 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.sub_item_link,
.subsub_item_link {
  width: 100%;
  height: 30px;
  color: var(--gray);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-right: 1px solid var(--black);
  padding: 0 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.item_link:hover,
.sub_item_link:hover,
.subsub_item_link:hover {
  color: var(--yellow);
}

.icon-outline {
  width: 20px;
  height: 20px;
}

.sub_prod_list_container {
  border-right: 6px solid var(--gray--light);
}

.item_link:first-child {
  border-top: 1px solid var(--yellow);
}

.prod_catg {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
  flex-grow: 1;
}

.prod_catg p,
.sub_prod_catg p,
.subsub_prod_catg p {
  font-size: 1.6rem;
  font-weight: 400;
}

.catg_list {
  min-width: 312px;
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  padding: 0 10px;
  /* user-select: none; */
}

.search_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.search_wrapper input {
  font-size: 1.6rem;
  width: 837px;
  height: 40px;
  padding: 0 20px;
  border-radius: 33px;
  border: none;
  outline: none;
}

.search_icon {
  position: absolute;
  top: 0;
  right: 0;
}

.search_icon svg {
  border-top-right-radius: 33px;
  border-bottom-right-radius: 33px;
}

/* Mega Links */

.dropdown_wrapper {
  position: absolute;
  display: flex;
  flex-direction: row;
  z-index: 999;
}

.catalog_navbar_container {
  display: none;
}

.catalog_navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2.2rem;
  padding: 20px 15px;
  background: var(--white);
}

.submenu {
  width: 868px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  background-color: var(--white);
  margin-top: 5px;
  padding-top: 5px;
  padding-left: 15px;
  height: 434px;
  position: relative;
  border-left: 6px solid var(--gray--light);
  border-right: 6px solid var(--gray--light);
}

.image-item {
  text-align: center;
  grid-column: 3;
}

.image-item img {
  min-width: 250px;
  max-height: 186px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.item-title {
  color: var(--black--primary);
  font-weight: 600;
  font-size: 1.6rem;
  margin-bottom: 8px;
}

.prod_list_container {
  width: 100%;
}

.products-list {
  list-style-type: none;
}

.products-list li {
  margin-bottom: 4px;
}

.products-list li a {
  text-decoration: none;
  font-weight: 400;
  font-size: 1.6rem;
  color: var(--gray);
}

/* HomeSlider */

.slide_home {
  width: 100vw;
  height: 440px;
}

.slide_home img {
  width: 100%;
  height: 100%;
}

.swiper .swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  background-color: var(--yellow);
  margin: 0 16px !important;
}

.swiper .swiper-pagination-bullet-active {
  transform: scaleX(3.5);
  transition: 0.8s;
  border-radius: 15%;
}


/* Three Category */

.three_container {
  max-width: 1180px;
  margin: 40px auto 70px;
  display: flex;
  justify-content: space-between;
  gap: 5px;
}

.three_card {
  width: 343px;
  background: var(--gray--light1);
  display: flex;
  padding: 16px;
  gap: 16px;
  border-radius: 4px;
}

.three_img {
  width: 155px;
  height: 138px;
}

.three_img img {
  width: 100%;
  height: 100%;
}

.three_text {
  width: 138px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.three_text_head {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 200;
  text-transform: uppercase;
}

.three_link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.three_link p {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 700;
  text-decoration: underline;
}

.three_span1 {
  color: var(--black--primary);
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.three_span2 {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
}

.icon_arrow {
  width: 18px;
  height: 18px;
  text-align: center;
  color: var(--white);
  background: var(--yellow);
  margin-left: 15px;
  border-radius: 50%;
  font-weight: 900;
}

/* Title */

.title_container {
  max-width: 1180px;
  margin: 0 auto;
}

.title_name {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  column-gap: 15px;
}

.title_name li {
  color: var(--gray);
  font-size: 2rem;
  font-weight: 300;
  margin-bottom: 10px;
  padding: 0 10px;
  cursor: pointer;
  list-style: none;
}

.title_name .active_title_name {
  color: var(--white);
  background-color: var(--yellow);
  border: 1px solid var(--yellow);
  border-radius: 15px;
}

/* Products */

.products_container {
  max-width: 1180px;
  margin: 0 auto;
}

.products_box {
  margin: 50px 0 60px;
  display: grid;
  /* grid-template-columns: repeat(auto-fill, minmax(220px, 1fr)); */
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 40px 20px;
}

.products_card {
  /* min-width: 220px; */
  max-width: 220px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 8px;
  padding: 20px 28px;
  border: 1px solid #f4f4f4;
  border-radius: 8px;
  cursor: pointer;
}

.products_card:hover{
  border: 1px solid var(--yellow);
}
.products_card_category {
  /* min-width: 220px; */
  max-width: 220px;
  padding: 20px 28px;
  border-right: 1px solid var(--gray--light2);
}

.products_card h6 {
  color: var(--gray--darker);
  font-size: 1.2rem;
  font-weight: 400;
}
.products_card_category h3 {
  margin-top: 20px;
  margin-left: 10px;
  color: var(--gray--darker);
  font-weight: 400;
  font-size: 1.8rem;
  cursor: pointer;
}
.products_card_category h3:hover {
  color: var(--yellow);
}
.sub_category_css ul li {
  cursor: pointer;
  list-style: none;
  color: var(--gray--darker);
  font-weight: 400;
  font-size: 1.5rem;
}
.sub_category_css ul li:hover {
  color: var(--yellow);
}

.prod_name {
  color: var(--blue);
  font-size: 1.4rem;
  font-weight: 700;
  word-wrap: break-word;
}

.product_img {
  /* display: block;
  margin-top: auto; */
  display: flex;
  align-items: flex-start;
  max-width: 165px;
  height: auto;
}

.product_img img {
  width: 100%;
  height: 100%;
}

.prod_itself {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
}

.first_price1 {
  color: var(--gray--darker);
  font-size: 1.2rem;
  font-weight: 400;
}

.sale_price {
  color: var(--red);
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 3px;
}

.first_price {
  color: var(--black--primary);
  font-size: 2rem;
  font-weight: 400;
}

.view_all {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.see_all {
  display: block;
  margin-left: auto;
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 400;
}

.see_all::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 123.5px;
  height: 1px;
  background-color: var(--black--primary);
}

/* Partners */

.partners_img {
  width: 120px;
  height: 120px;
  padding: 25px;
  border: 1.2px solid var(--gray--light2);
  border-radius: 12px;
}

.partners_img img {
  width: 100%;
  height: 100%;
}

.slide_container {
  max-width: 1180px;
  margin: 60px auto 85px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  padding: 0 1px;
}

.partners_slide {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 56px;
  animation: slide 9s infinite linear;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-32%);
  }
}

.slide_container:hover .partners_slide {
  animation-play-state: paused;
}

/* Footer */

footer {
  padding: 20px 0;
  background: var(--black--primary);
}

.footer_container {
  max-width: 1180px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}

.contact,
.social_net {
  display: flex;
  flex-direction: column;
  color: var(--white);
}

.contact h6,
.social_net h6 {
  font-size: 1.6rem;
  font-weight: 600;
}

.contact p {
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 15px;
}

.social_net ul li {
  list-style-type: none;
}

.social_net ul {
  display: flex;
  gap: 23px;
  margin-top: 15px;
}

/* Filter */

.slc_ctg_container {
  max-width: 1180px;
  margin: 25px auto 95px;
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.slc_ctg_filter {
  position: sticky;
  top: 10px;
  width: 284px;
  height: 600px;
  overflow-y: scroll;
  padding: 10px;
}

.slc_ctg_filter::-webkit-scrollbar {
  width: 2px;
}
.slc_ctg_filter::-webkit-scrollbar-track {
  background: var(--gray--light2);
}
.slc_ctg_filter::-webkit-scrollbar-thumb {
  background: var(--black--primary);
}

.slc_ctg_box {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.slc_prod_name {
  display: flex;
  align-items: center;
  gap: 9px;
  padding: 7px 20px;
}

.slc_prod_name h3 {
  color: var(--black--secondary);
  font-size: 2rem;
  font-weight: 600;
}

.slc_prod_name span {
  color: var(--black--primary);
  font-size: 1.6rem;
  font-weight: 500;
}

.slc_filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid var(--gray--light2);
}

.slc_filter_resp_container {
  display: none;
}

.slc_filter_responsive {
  display: none;
  width: 160px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid var(--gray--light2);
  border-radius: 8px;
  padding: 12px 26px;
  cursor: pointer;
}

.slc_filter p,
.slc_filter_responsive p {
  color: var(--black--secondary);
  font-size: 1.6rem;
  font-weight: 500;
}

.slc_respon_container {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.filter_btn {
  display: none;
}

.close_filter {
  display: none;
}

.slc_price_context {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  padding: 0 20px 21px;
  border-bottom: 1px solid var(--gray--light2);
}

.slc_price_context_name {
  /* user-select: none; */
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.slc_price_context_name h4 {
  color: var(--black--third);
  font-size: 1.4rem;
  font-weight: 600;
}

.price_input {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.price_field {
  width: 90px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 4px;
  border-radius: 10px;
  color: var(--black--secondary);
  background-color: #f3f3f3;
}

.price_field input {
  width: 45px;
  border: none;
  outline: none;
  background-color: #f3f3f3;
  color: var(--black--secondary);
  text-align: right;
  font-size: 1.3rem;
  font-weight: 500;
}

.price_field span {
  font-size: 1.6rem;
  width: 16px;
  height: 22px;
  margin-left: 3px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.price_input .separator {
  color: var(--gray--light2);
}

.slider {
  width: 100%;
  height: 2px;
  background: var(--yellow);
}

.slider .thumb {
  width: 17px;
  height: 17px;
  cursor: grab;
  background: var(--white);
  border-radius: 50%;
  border: 2px solid var(--yellow);
  outline: none;
  top: -8px;
}

.phoneName_container {
  max-height: 100%;
  overflow-y: scroll;
  display: none;
  flex-direction: column;
  transition: max-height 0.3s ease-in-out;
  gap: 16px;
  margin-top: 20px;
}

.phoneName_container.active {
  display: flex; /* Показываем элемент, если есть класс 'active' */
  max-height: 500px;
}

.phoneName_container::-webkit-scrollbar {
  width: 2px;
}

.phoneName_container::-webkit-scrollbar-track {
  background: var(--gray--light2);
}

.phoneName_container::-webkit-scrollbar-thumb {
  background: var(--black--primary);
}

.phoneName_boxes {
  color: var(--black--secondary);
  font-size: 1.6rem;
  font-weight: 400;
  display: flex;
  align-items: center;
}

.phoneName_box {
  display: flex;
  align-items: center;
}

.phoneName_box label {
  margin-left: 9px;
  font-weight: 400;
}

.custom-checkbox {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 18px;
  height: 18px;
  background-color: var(--white);
  border: 2px solid var(--yellow);
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 24px;
}

.custom-checkbox:checked {
  background-color: #fff;
  border-color: var(--yellow);
}

.custom-checkbox:checked::before {
  content: "\2713";
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--yellow);
  font-size: 16px;
  font-weight: 600;
}

.diff_prod_box {
  max-width: 832px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 30px 20px;
}


.diff_prod_card {
  max-width: 264px;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border: 1px solid #f4f4f4;
  border-radius: 10px;
  transition: all 0.5s ease;
}
.diff_prod_card2{
  max-width: 277px;
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border: 1px solid #f4f4f4;
  border-radius: 10px;
  transition: all 0.5s ease;
}
.diff_prod_card:hover {
  border: 1px solid var(--yellow);
}

.diff_prod_img {
  max-width: 244px;
  height: auto;
}

.diff_prod_img img {
  width: 100%;
  height: 100%;
}

.diff_prod_name {
  color: var(--black--primary);
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 35px;
}

.diff_prod_price_cont {
  display: block;
  margin-top: auto;
}

.diff_prod_price {
  color: var(--gray--secondary);
  font-size: 1.4rem;
  font-weight: 700;
}

.diff_prod_sale1 {
  color: var(--red);
  font-size: 1.6rem;
  font-weight: 700;
  margin-top: 3px;
  margin-bottom: 5px;
}

.diff_prod_price1 {
  color: var(--black--primary);
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 5px;
}

/* Single Product page */

.single_container {
  max-width: 1180px;
  margin: 0 auto;
  margin-top: 24px;
}

.single_head {
  display: flex;
  align-self: stretch;
  color: #111827;
  font-size: 2rem;
  font-weight: 600;
}

.single_box {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  margin: 70px 0;
  position: relative;
}

.single_prod_arrows {
  display: flex;
  align-items: center;
  position: relative;
}

.single_arrowback,
.single_arrowforward {
  position: absolute;
  width: 50px;
  height: 70px;
  color: var(--yellow);
  cursor: pointer;
  z-index: 1;
}
.single_arrowforward {
  right: -24px;
}
.single_arrowback {
  left: -24px;
}

.single_prod_img {
  max-width: 440px;
  height: 440px;
}

.single_prod_img img {
  max-width: 100%;
